import React from 'react';
import {Route} from 'react-router-dom';
import HomePage from './HomePage';
import CategoryPage from './CategoryPage/CategoryPage';
import FullPostPage from '../../Components/FullPostPage';
import FavouritedPostsPage from './LikedPosts/FavouritedPostsPage';

const Main = () =>{
	return(
		<main id="main">
			<Route path="/" exact component={HomePage}/>
			<Route 
				path="/category/:category" 
				render = {(props)=> <CategoryPage category={props.match.params.category}/>}
			/>
			<Route 
				path="/article/:id" 
				render= {(props)=> <FullPostPage id={props.match.params.id}/>}                  
			/>
			<Route path="/favourited" component={FavouritedPostsPage}/>
		</main>    
	)
}

export default Main